#about.container-fluid {
    max-width: 100%;
    background-color: var(--color-secondary);
    overflow: hidden;
    .container {
        @extend %container;
        .about-title {
            height: 100px;
            position: relative;
            h3 {
                margin: 0;
                font-size: 35px;
                color: var(--color-primary);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%); } }
        .row {
            display: flex;
            .col-img {
                display: none;
                @include bp-mobile(laptop) {
                    display: block;
                    flex: 1 0 30%; }
                img {
                    width: 100%;
                    height: auto;
                    border: 5px solid var(--color-primary);
                    border-radius: 500px;
                    -webkit-border-radius: 500px;
                    -moz-border-radius: 500px; } }
            .col-content {
                font-size: 14px;
                width: 100%;
                @include bp-mobile(laptop) {
                    margin-left: 5%;
                    font-size: 16px; }
                .about-content {
                    width: 100%;
                    &_item {
                        display: flex;
                        margin-bottom: 40px;
                        .item_left {
                            min-width: 100px;
                            padding: 12px 10px;
                            background-color: var(--color-primary);
                            .svg-inline--fa {
                                // font-size: 14px
                                display: inline;
                                color: #fff;
                                margin-right: 5px; }
                            span {
                                // font-size: 14px
                                font-weight: 700;
                                color: #fff; } }
                        .item_right {
                            width: 100%;
                            padding: 12px 10px;
                            border-bottom: 3px solid var(--color-primary);
                            p {
                                margin: 0;
                                // font-size: 14px
                                color: #000; } } } } } } } }

