#skills.container-fluid {
  width: 100%;
  min-height: 600px;
  height: auto;
  background: var(--color-primary);
  background: linear-gradient(90deg, rgba(255,133,83,1) 0%, rgba(255,133,83,1) 35%, rgba(255,255,255,1) 100%);
  .container {
    @extend %container;
    padding: 50px 0;
    .skills-title {
      height: 100px;
      position: relative;
      h3 {
        margin: 0;
        font-size: 35px;
        color: var(--color-black);
        @extend %center-posxy; } }
    .skills-content {
      display: flex;
      .skills_img {
        display: none;
        @include bp-mobile(laptop) {
          display: block;
          width: 700px;
          height: 520px;
          img {
            min-width: 100%;
            height: 100%; } } }
      .skills_rec {
        width: 100%;
        position: relative;
        .html-position {
          position: absolute;
          left: 0;
          top: 4%; }

        .javascript-position {
          position: absolute;
          left: 0;
          top: 16%; }

        .github-position {
          position: absolute;
          left: 0;
          top: 28%; }

        .pythonjava-position {
          position: absolute;
          left: 0;
          top: 40%; }

        .lang-position {
          position: absolute;
          left: 0;
          top: 52%; }

        .skills-items {
          background-color: var(--color-secondary);
          height: 45px;
          border-radius: 45px;
          display: flex;
          width: 100%;
          position: relative;
          transition: all 0.2s;
          &:hover {
            box-shadow: 0 0 10px 1px rgba(8, 8, 8, 0.541);
            transition: all 0.2s; }
          .progress-bar {
            width: var(--value);
            background: linear-gradient(#00ff04, #09aad3);
            animation: progressAnimation 2s;
            border-radius: 45px;
            overflow: visible; }
          .skills-items-img {
            width: 45px;
            height: 45px;
            background-color: #46f0e779;
            border-radius: 45px;
            border: none;
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            min-width: 45px;
            position: absolute;
            z-index: 2;
            img {
              width: 40px;
              height: 40px;
              height: auto;
              vertical-align: middle;
              border-style: none; } }
          .skills-items-text {
            position: absolute;
            z-index: 2;
            left: 45px;
            width: 100%;
            h2 {
              font-size: 14px;
              height: 45px;
              line-height: 45px;
              padding-left: 5px;
              @include bp-mobile(tablet) {
                font-size: 18px;
                padding-left: 15px; } } } } } } } }

@-webkit-keyframes progressAnimation {
  0% {
    width: 5%;
    background-color: #F9BCCA; }
  100% {
    width: var(--value);
    background-color: #EF476F; } }

@-moz-keyframes progressAnimation {
  0% {
    width: 5%;
    background-color: #F9BCCA; }
  100% {
    width: var(--value);
    background-color: #EF476F; } }
