#resume.container-fluid {
    width: 100%;
    background-color: var(--color-secondary);
    .container {
        @extend %container;
        padding: 50px 0;
        .resume-title {
            height: 100px;
            width: 100%;
            position: relative;
            h2 {
                margin: 0;
                font-size: 35px;
                color: var(--color-primary);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%); } }
        .resume_content {
            display: flex;
            flex-direction: column;
            @include bp-mobile(tablet) {
                flex-flow: row; }
            .resume-education,.resume-experience {
                width: 100%;
                margin-bottom: 20px;
                @include bp-mobile(tablet) {
                    flex: 0 0 50%; }
                h3 {
                    font-size: 20px;
                    color: var(--color-primary);
                    margin-bottom: 20px;
                    transition: all 0.5s;
                    text-transform: uppercase;
                    font-weight: 700; }
                &:hover {
                    h3 {
                        letter-spacing: 1.8px;
                        color: #1f5297;
                        transition: all 0.5s; } }
                .resume-items {
                    border-left: 2px solid #1f5297;
                    position: relative;
                    padding: 0 30px 10px;
                    line-height: 2;
                    font-size: 16px;
                    p:first-child {
                        text-transform: uppercase;
                        font-weight: 600; }
                    h4 {
                        background-color: #e4edf9;
                        display: inline-block;
                        padding: 0 10px; }
                    &::before {
                        content: "";
                        position: absolute;
                        width: 14px;
                        height: 14px;
                        border-radius: 50px;
                        left: -8px;
                        top: 0;
                        background: var(--color-primary);
                        border: 2px solid #1f5297; }
                    p:last-child {
                        font-family: monospace; } } } } } }
