.home__container {
    background-image: url(../images/bg.png);
    height: auto;
    padding: 30px 0;
    .container {
        @extend %container;
        display: flex;
        flex-flow: column;
        @include bp-mobile(mobile) {
            flex-direction: row; }
        .home-right {
            align-self: center;
            @include bp-mobile(mobile) {
                flex: 0 0 50%;
                max-width: 100%; }
            h2 {
                font-size: calc(.8rem + 2vw);
                font-weight: 400;
                color: var(--color-secondary);
                line-height: strip-unit(calc(2rem + 4vw));

                span {
                    text-transform: uppercase; } }
            p {
                width: 22ch;
                animation: typing 2s steps(22), blink .5s step-end infinite alternate;
                white-space: nowrap;
                overflow: hidden;
                border-right: 3px solid;
                font-family: monospace;
                font-size: calc(1rem + 1vw); } }
        .home-left {
            max-width: 75%;
            text-align: center;
            align-self: center;
            @include bp-mobile(mobile) {
                flex: 0 0 50%;
                width: 100%; }
            img {
                max-width: 75%;
                height: auto;
                border-radius: 50%;
                border: 5px solid #fff;
                box-shadow: 0 5px 12px rgba(0,0,0,0.6);
                vertical-align: middle;
                margin-bottom: 20px;
                @include bp-mobile(mobile) {
                    margin-bottom: 0; }
                &:hover {
                    -webkit-filter: grayscale(100%);  //- Safari 6.0 - 9.0 -//
                    filter: grayscale(100%); } } } } }
