%container {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    @include bp-mobile(tablet) {
        width: 80% !important; } }

%h3 {
    margin: 0;
    font-size: calc(2rem + 1vw);
    text-align: center;
    color: var(--color-secondary);
    padding: 30px;


 }    //- center with flexbox
%center-flex {
    display: flex;
    align-items: center;
    justify-content: center; }

//- center with grid
%center-grid {
    display: grid;
    place-items: center; }

//- center with position
%center-posx {
    position: absolute;
    left: 50%;
    transform: translateX(-50%); }

%center-posy {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }

%center-posxy {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%); }

//- text overflow with ...
%text-overflow {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }

//- Multiple line text overflow with ...
%text-overflow-mul {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    //- change number of row
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis; }
