#myblog.container-fluid {
    width: 100%;
    .container {
        @extend %container;
        h3 {
            margin: 0;
            padding: 40px 0;
            font-size: 36px;
            color: var(--color-primary);
            text-align: center;
            margin-bottom: 30px;
            background: rgb(255,133,83);
            background: linear-gradient(180deg, rgba(255,133,83,1) 0%, rgba(255,133,83,0.41530115464154416) 21%, rgba(255,255,255,1) 50%); }
        .blog {
            gap: 30px;
            @include bp-mobile(tablet) {
                display: flex; }
            .head-content {
                gap: 20px;
                position: relative;
                .content-title {
                    font-size: 14px;
                    color: #333;
                    line-height: 25px;
                    margin-bottom: 15px;
                    h4 {
                        font-size: 20px;
                        margin-bottom: 10px;
                        line-height: 30px;
                        text-decoration: underline;
                        text-decoration-color: var(--color-primary);
                        text-decoration-thickness: 2px; }
                    img {
                        max-width: 100%;
                        height: auto;
                        object-fit: cover; } } }
            .read-more {
                position: relative;
                display: block;
                padding-bottom: 10px;
                margin-top: 10px;
                h5.h5-style {
                    font-size: 14px;
                    line-height: 30px;
                    // color: #fff
                    background-color: var(--color-primary);
                    height: 30px;
                    padding-left: 5px;
                    padding-right: 30px;
                    width: fit-content;
                    vertical-align: middle;
                    color: transparent;
                    cursor: pointer;
                    &::after {
                        position: absolute;
                        top: 0;
                        left: 107.5px;
                        content: "";
                        width: 0;
                        height: 0;
                        border-top: 15px solid transparent;
                        border-left: 15px solid var(--color-primary);
                        border-bottom: 15px solid transparent; }
                    &::before {
                        // content: "Đọc nội dung"
                        content: attr(title);
                        position: absolute;
                        top: 0;
                        left: 5px;
                        color: #fff !important;
                        z-index: 9999999; } } }
            .blog-content {
                display: block;
                width: 100%;
                font-size: 14px;
                font-weight: 700;
                color: #333;
                line-height: 30px;
                p:first-child {
                    font-weight: 500; }
                img {
                    max-width: 100%;
                    height: auto;
                    object-fit: cover; }
                .video-wrapper {
                    position: relative;
                    padding-bottom: 56.25%;
                    height: 0;
                    overflow: hidden;
                    iframe {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%; } } }
            #blog-content01 {
                display: none; }
            #blog-content02 {
                display: none; }
            .blog01, .blog02 {
                // display: none
                margin-bottom: 50px;
                box-shadow: 0 4px 16px rgba(0,0,0, 0.3);
                padding: 15px;
                overflow: auto;
                @include bp-mobile(tablet) {
                    flex: 1 0 calc(50% - 20px); } } } } }





