$breakpoint-list: ("mobile": 576px, "tablet": 768px, "laptop": 1200px, "desktop": 1400px);
//- responsive mobile first
@mixin bp-mobile($class) {
    @if $class == mobile {
        @media (min-width: map-deep-get($breakpoint-list, $class)) {
            @content; } }

    @else if $class == tablet {
        @media (min-width: map-deep-get($breakpoint-list, $class)) {
            @content; } }

    @else if $class == laptop {
        @media (min-width: map-deep-get($breakpoint-list, $class)) {
            @content; } }

    @else if $class == desktop {
        @media (min-width: map-deep-get($breakpoint-list, $class)) {
            @content; } } }
