#portfolio.container-fluid {
    max-width: 100vw;
    background-color: var(--color-secondary);
    .container {
        @extend %container;
        padding: 50px 0; }
    .portfolio-title {
        height: 100px;
        position: relative;
        h3 {
            margin: 0;
            font-size: 35px;
            color: var(--color-primary);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } }
    .swiper-container {
        width: 100%;
        height: 100%;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        padding-bottom: 100px;
        overflow-x: hidden; }
    .swiper-wrapper {
        .swiper-slide {
            font-size: 18px;
            background: transparent;
            display: -webkit-box;
            display: -ms-flexbox;
            display: -webkit-flex;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            -webkit-justify-content: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            -webkit-align-items: center;
            align-items: center;
            align-self: center;
            img {
                max-height: 500px;
                max-width: 80%;
                border: 1px solid #616aaa;
                box-shadow: 0 2px 2px -10px rgba(0, 49, 52, 0.206), 0 7px 5px -10px rgba(0, 49, 52, 0.304), 0 10px 15px -10px rgba(0, 49, 52, 0.51);
                @include bp-mobile(tablet) {
                    box-shadow: 0 2px 2px -20px rgba(0, 49, 52, 0.206), 0 7px 5px -20px rgba(0, 49, 52, 0.304), 0 30px 25px -20px rgba(0, 49, 52, 0.51);
                    border-radius: 30px; } }
            .slide_content {
                position: absolute;
                bottom: -55px;
                left: 50%;
                transform: translateX(-50%);
                max-height: 200px;
                min-width: 320px;
                width: auto;
                .slide-text {
                    display: none; }
                .btn-view {
                    text-align: center;
                    a.button {
                        display: inline-block;
                        margin-right: 10px;
                        font-size: 16px;
                        line-height: 1;
                        padding: 8px 24px;
                        border-radius: 5px;
                        border: 1px solid #c75627;
                        color: var(--color-secondary);
                        transition: all 0.7s;
                        font-weight: 600;
                        text-decoration: none;
                        background-color: #c75627;
                        &:hover {
                            background-color: transparent;
                            color: #c75627;
                            transition: all 0.3s; }
                        &:last-child {
                            margin-right: 0; } } } }
            @include bp-mobile(tablet) {
                .slide_content {
                    bottom: -40px;
                    left: calc(70% - 70px);
                    border: 1px solid;
                    border-color: var(--color-primary);
                    line-height: 1.5;
                    background-color: var(--color-primary);
                    border-radius: 10px;
                    padding: 10px 20px;
                    opacity: 0.95;
                    color: #000000;
                    box-shadow: 0 0 5px 0 var(--color-primary);
                    .slide-text {
                        display: block;
                        h5 {
                            margin: 0;
                            margin-bottom: 10px;
                            text-align: left;
                            color: #000000;
                            font-size: 18px; }
                        p {
                            margin: 0;
                            text-align: left;
                            font-size: 16px;
                            margin-bottom: 10px; } }
                    .btn-view {
                        a.button {
                            border-color: var(--color-secondary);
                            &:hover {
                                background-color: var(--color-secondary); } } } } } } }
    .swiper-button-next,.swiper-button-prev {
        --swiper-navigation-size: 30px;
        top: 35%;
        @include bp-mobile(tablet) {
            --swiper-navigation-size: 44px !important;
            top: 40% !important; } }
    .swiper-button-next {
        right: 10px !important; }
    .swiper-button-prev {
        left: 10px; } }
